export const DetailModal = ({ handleOk, handleCancel, modal, onDelete, onUpdate, detail }) => {
  const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  };

  const validateMessages = {
    required: " .",
    types: {
      number: "  ",
    },
    number: {
      range: "Must be between ${min} and ${max}",
    },
  };
  useEffect(() => {
    return () => {
      detail = "null";
    };
  }, []);
  return (
    <Modal
      title="Basic Modal"
      visible={modal}
      onOk={handleOk}
      onCancel={handleCancel}
      footer={[
        <Button
          type="danger"
          key={"delete"}
          onClick={() => {
            onDelete(detail.id);
          }}
        >
          
        </Button>,
        <Button key="back" onClick={handleCancel}>
          
        </Button>,
      ]}
    >
      <Form {...layout} name="addTransaction" onFinish={onUpdate} validateMessages={validateMessages}>
        <Form.Item name={"id"} initialValue={detail ? detail.id : null} style={{ display: "none" }}>
          <InputNumber disabled />
        </Form.Item>
        <Form.Item
          name={"type"}
          initialValue={detail ? detail.type : ""}
          label="/"
          rules={[{ required: true }]}
        >
          <Select>
            <Select.Option value="deposit"></Select.Option>
            <Select.Option value="expense"></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name={"amount"}
          initialValue={detail ? detail.amount : "0"}
          label=""
          rules={[{ required: true }]}
        >
          <InputNumber min={0} />
        </Form.Item>
        <Form.Item name={"order"} initialValue={detail ? detail.order : null} label="">
          <InputNumber min={0} disabled />
        </Form.Item>
        <Form.Item
          name={"content"}
          initialValue={detail ? detail.content : "0"}
          label=""
          rules={[{ required: true }]}
        >
          <Input.TextArea />
        </Form.Item>
        <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 8 }}>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </Modal>
  );
};
